.SeatBooked{
    width:400px;
    background-color:rgb(250, 245, 245);
    margin:auto;
    margin-top:5%;
    padding: 10px;
    border-radius: 3px;
    transition: all 0.3s ease-in-out;

}

.SeatView{
    display: flex;
    flex-direction: row;
    justify-content: center
}
.SeatView > div{
    background-color: white;
    padding-top: 5px;
    width: 50px;
    height:30px;
    margin: 10px;
    text-align: center;
    line-height: 15px;
    font-size: 30px;
    font-style: italic;
    font-weight: 300;
    vertical-align: middle;
    border-radius: 2px;
    border-style: outset;
    cursor: pointer;
}
.SeatView .booked{
    background-color: rgb(248, 2, 2);
}

.SeatView .unbooked{
    background-color: white;
}

.SeatView .Gap{
    width: 20px;
    background-color:transparent;
    border-style: none;
    cursor: unset;

}
.SeatView .Gap:hover{
    width: 20px;
    background-color:transparent;
    border-style: none;

}
.Backdrop{
    width:100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left:0;
    top:0;
    background-color: rgba(0,0,0,0.5);
    text-align: center;
}


